/* ==================== Media queries ======================= */

/*==========  Desktop First Method  ==========*/

/* Large Devices, Wide Screens */
@media only screen and (max-width : 1440px) {
    .top10-heading {
        font-size: 32px;
    }

    .top10-slide_img {
        left: 41%;
    }

    .table-container {
      width: 44%;
      padding: 3% 2.5% 3%;
      border: 12px solid $bright;
    }

    .slide-description_top10 h4 {
        font-size: 22px;
    }

    .top10-slide_info {
        right: -78%;
    }
}

@media only screen and (max-width : 1300px) {
    
    .catalog-section:after,
    .catalog-section .dotted-decor_left {
      width: 1%;
    }

    .catalog-section:before,
    .catalog-section .dotted-decor_right {
      width: 2%;
    }
}

@media only screen and (max-width : 1200px) {
    .scale-actual {
      width: 160px;
      height: 120px;
      line-height: 120px;
      font-size: 90px;
    }

    .square-scale {
        flex-basis: 85%;
    }

    .scale-down span,
    .scale-up span {
        font-size: 30px;
    }

    .trust-counter {
        margin: 0 20px 20px 0;
    }

    .advantages-item {
        max-width: 470px;
        flex: 1 1 350px;
    }

    .feature-img_container .bag-logo .logo {
        font-size: 50px;
    }

    .present-box-1 {
        width: 370px;
        top: 57%;
    }

    .present-box-2 {
        width: 330px;
        bottom: -70px;
    }

    .present-box-3 {
        width: 200px;
        right: 0;
        bottom: 60%;
    }

    .coin-1 {
        width: 380px;
        top: 60%;
    }

    .coin-2 {
        width: 330px;
        bottom: -90px;
    }

    .coin-3 {
        width: 200px;
        bottom: 60%;
    }

    .slide-description_top10 {
        //position: static;
    }

    .category-slide-item {
        padding-top: 10px;
        padding-left: 10px;
    }

    .top10-heading .accent {
        font-size: 48px;
    }

    .top10-slide_img {
        position: static;
        width: 100%;
        max-width: 100%;
        top: 0;
        left: 0;
        margin-bottom: 20px;
        z-index: 0;
    }

    .table-container {
        top: -100px;
        left: 20px;
        width: 100%;
        max-width: 420px;
    }

    .slider-arrows_top10 {
        bottom: 500px;
    }

    .top10-slide_info {
        right: auto;
        left: 0;
        top: 104%;
        width: 100%;
        max-width: 100%;
    }

    .footer-top {
        flex-wrap: wrap;
    }

    .footer-bottom {
        flex-wrap: wrap;
        .button-container_footer {
            flex-basis: 100%;
            width: 100%;
        }
    }
}

@media only screen and (max-width : 1024px) {
    .header-phone {
        padding-left: 10px;
    }

    .heading_lg {
        font-size: 68px;
    }

    .calculator-heading {
        text-align: center;
        padding-top: 120px;
        margin-bottom: 20px;
    }

    .calculator-content {
        flex-wrap: wrap;
        justify-content: center;
        padding-bottom: 120px;
    }

    .trust-text .heading {
        font-size: 55px;
    }

    .catalog-section:after,
    .catalog-section .dotted-decor_left,
    .catalog-section:before,
    .catalog-section .dotted-decor_right {
      display: none;
    }

    .parallaxed-1,
    .parallaxed-2,
    .parallaxed-3 {
        opacity: 0.3;
    }

    .categories-list {
        margin-bottom: 0;
    }

    .categories-list li {
        margin-bottom: 10px;
        flex-basis: 30%;
        padding-left: 10px;
        padding-right: 10px;
    }

    .slide-description {
        padding: 2%;
        max-width: 355px;
        h4 {
            font-size: 24px;
        }
    }

    .facts-board {
        max-width: 400px;
        padding: 3.5% 4.4% 3.5%

    }

    .steps-container {
        flex-wrap: wrap;
    }

    .step-item {
        flex-basis: 48%;
    }

    .footer h5 {
        margin-bottom: 10px !important;
        font-family: $bold;
        color: $xlight;
    }

    .footer-bottom {
        justify-content: flex-start;
    }

    .footer-bottom .logo.logo_footer {
        flex-basis: 100%;
        width: 100%;
    }
}

/* Small Devices, Tablets */
@media only screen and (max-width : 767px) {

    .slide_item {
      position: relative;
      min-height: 0px;
      img {
        position: relative;
        width: 100%;
      }
    }
    
    .header_wrapper-main {
        position: static;
        transform: none;
        padding-top: 45px;
    }

    .form_bg {
        background-image: none;
        right: 5px;
        left: auto;
        width: auto;
        height: auto;
    }

    .form_container {
        padding-top: 45px;
        position: static;
        background: $xlight;
        border-radius: 10px;
        margin-bottom: 20px;
    }

    .heading_lg {
        font-size: 35px;
    }

    .logo_container {
        white-space: normal;
        text-align: left;
        .logo-text {
            padding: 10px 0;
        }
    }

    .menu-btn_container {
        white-space: normal;
        text-align: right;
    }

    .menu-btn {
        width: 70px;
        height: 70px;
        line-height: 70px;
        font-size: 15px;
        margin-bottom: 10px;
    }

    .trust-text {
        max-width: 100%;
        p {
            max-width: 100%;
        }
    }

    .advantages-list {
        justify-content: center;
    }

    .slide-description {
        position: relative;
        top: -60px;
    }

    .parallaxed-1,
    .parallaxed-2,
    .parallaxed-3 {
        display: none;
    }

    .feature-content {
        flex-wrap: wrap;
    }

    .insurance-section .insurance-img {
        position: static;
        width: 100%;
    }

    .feature-text {
        padding-top: 11px;
    }

    .feature-text p {
        font-size: 14px;
    }

    .about-img {
        width: 100%;
    }

    .facts-board {
        position: relative;
        top: -30px;
        left: 30px;
    }

    .questions-section {
        min-height: auto;
        max-height: none;
        height: 480px;
    }

    .footer-list_pages .pages-links {
        justify-content: flex-start;
    }

    .pages-links li {
        flex-basis: 40%;
    }
}

@media only screen and (max-width : 600px) {
    .header .header-phone {
        display: none;
    }

    h2 {
        font-size: 28px !important;
    }

    .button {
        font-size: 14px;
    }

    .heading {
        font-size: 28px !important;
    }

    .form_bg {
        position: static;
    }

    .form_container {
        margin: 0 auto 20px;
    }

    .header_wrapper-main {
        text-align: center;
    }

    .scale-actual {
      width: 90px;
      height: 70px;
      line-height: 70px;
      font-size: 61px;
    }

    .scale-down,
    .scale-up {
        flex-basis: 100px;
        span {
            font-size: 22px;
            &:before,
            &:after {
                margin: 0 5px;
            }
        }
    }

    .scale-btn_text {
      font-size: 16px;
      padding: 0 15px;
    }

    .approximate-cost_text {
        font-size: 14px;
    }

    .approximate-cost_value {
        font-size: 20px;
        padding-left: 10px;
        padding-right: 10px;
    }

    .accurate-cost {
        flex-wrap: wrap;
        justify-content: center;
    }

    .accurate-consist_btn {
        margin-right: 27px;
    }

    .categories-list li {
        flex-basis: 48%;
    }

    .table-container {
        top: 0;
        left: 0;
        border-width: 3px;
    }

    .item-cost-table {
        font-size: 13px;
    }

    .slider-arrows {
        bottom: auto;
        top: 10px;
    }

    .slide-description_top10 h4 {
        font-size: 18px;
    }

    .top10-slide_info {
        display: none;
    }

    .category-slide-item {
        padding: 0 !important;
    }

    .feature-img_container {
        flex-basis: 100%;
    }

    .facts-board {
        left: 5px;
        max-width: 270px;
        p {
            font-size: 13px;
        }
        .facts-year {
            font-size: 20px;
            margin-bottom: 2%;
        }
        .button {
            width: 240px;
        }
    }

    .steps-container {
        justify-content: center;
    }

    .step-item {
        flex-basis: 100%;
        max-width: 450px;
    }


    .testimonial-slide-item {
        padding: 110px 1% 5%;
    }

    .testimonial-photo {
        width: 180px;
        height: 180px;
        border-width: 12px;
    }

    .testimonial-name {
        font-size: 25px;
    }

    .testimonial-text {
        font-size: 13px;
    }

    .questions-section {
        height: auto;
        flex-wrap: wrap;
        justify-content: center;
        .heading_lg {
            text-align: center;
            margin: 15px 0;
        }
    }
}

/* Extra Small Devices, Phones */
@media only screen and (max-width : 400px) {
    .logo_container {
        max-width: 190px;
    }
    .categories-list li {
        flex-basis: 52%;
        font-size: 14px;
    }

    .pages-links li {
        flex-basis: 100%;
    }
}

/* Custom, iPhone Retina */
@media only screen and (max-width : 320px) {
    .table-container {
        width: 288px !important;
    }
}

/* Medium Devices, Desktops */
@media only screen and (min-width : 992px) {
    .calculator-section {
      animation: ruler 40s linear infinite;
    }

    .step-item:nth-child(1) {
      .step-num_value {
        animation: textColorBright 12s 3s linear infinite;
      }
      .step-num_name {
        animation: textColorDark 12s 3s linear infinite;
      }
      .step-text {
        animation: textColorMain 12s 3s linear infinite;
      }
      &:after {
        animation: stepsBg 12s 3s linear infinite;
      }
    }

    .step-item:nth-child(2) {
      .step-num_value {
        animation: textColorBright 12s 6s linear infinite;
      }
      .step-num_name {
        animation: textColorDark 12s 6s linear infinite;
      }
      .step-text {
        animation: textColorMain 12s 6s linear infinite;
      }
      &:after {
        animation: stepsBg 12s 6s linear infinite;
      }
    }

    .step-item:nth-child(3) {
      .step-num_value {
        animation: textColorBright 12s 9s linear infinite;
      }
      .step-num_name {
        animation: textColorDark 12s 9s linear infinite;
      }
      .step-text {
        animation: textColorMain 12s 9s linear infinite;
      }
      &:after {
        animation: stepsBg 12s 9s linear infinite;
      }
    }

    .step-item:nth-child(4) {
      .step-num_value {
        animation: textColorBright 12s 12s linear infinite;
      }
      .step-num_name {
        animation: textColorDark 12s 12s linear infinite;
      }
      .step-text {
        animation: textColorMain 12s 12s linear infinite;
      }
      &:after {
        animation: stepsBg 12s 12s linear infinite;
      }
    }

}

@media only screen and (min-width : 1920px) {
    .slide_item img {
        width: auto;
    }
}