/*------------------------------ steps section ------------------------------*/
.steps-section {
  .heading {
    margin-top: 10.8%;
  }
  .heading-notation {
    margin-bottom: 6.2%;
  }
}

.steps-container {
  justify-content: space-between;
  margin-bottom: 16.4%;
}

.step-item {
  position: relative;
  padding: 60px 3.1% 65px;
  margin-bottom: 25px;
  width: 23.2%;
  box-shadow: -7px 7px 20px rgba(0,0,0,.3);
  overflow: hidden;
  text-align: center;
  &:after {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    bottom: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: $bright;
    z-index: -1;
  }
}

.step-num_value {
  display: block;
  margin-bottom: 12px;
  font-size: 22px;
  color: $bright;
  font-family: $bold;
  transition: color .4s;
}

.step-num_name {
  display: block;
  margin-bottom: 18px;
  text-transform: uppercase;
  font-size: 19px;
  color: $xdark;
  font-family: $bold;
  transition: color .4s;
}

.step-text {
  font-size: 17px;
  line-height: 1.45;
  transition: color .4s;
}


@keyframes stepsBg {
    0% {left: -100%; right: 100%;}
    2% {left: 0%; right: 0%;}
    25% {left: 0%; right: 0%;}
    27% {left: 100%; right: -100%;}
    100% {left: 100%; right: -100%;}
}

@keyframes textColorMain {
    0% {color: $maintext;}
    2% {color: $xlight;}
    25% {color: $xlight;}
    27% {color: $maintext;}
    100% {color: $maintext;}
}

@keyframes textColorBright {
    0% {color: $bright;}
    2% {color: $xlight;}
    25% {color: $xlight;}
    27% {color: $bright;}
    100% {color: $bright;}
}

@keyframes textColorDark {
    0% {color: $dark;}
    2% {color: $xlight;}
    25% {color: $xlight;}
    27% {color: $dark;}
    100% {color: $dark;}
}


