/*------------------------------ footer ------------------------------*/

.footer {
  background: #333;
  color: $mlight;
  font-size: 14px;
  text-transform: uppercase;
  h5 {
    font-family: $regular;
    color: $mlight;
    margin-bottom: 25px;
    letter-spacing: 1.5px;
  }
}

.footer-top {
  padding: 6.9% 0 1.4%;
  justify-content: space-between;
  border-bottom: 1px solid #484848;
  .footer-list_pages,
  .footer-list_social{
    margin-bottom: 3%;
  }
}

nav {
  a {
    opacity: .8;
    transition: all .4s;
    &:hover,
    &:active {
      opacity: 1;
    }
  }

}

.footer-list_pages {
  flex-basis: 905px;
  .pages-links {
    margin-bottom: 5%;
  }
}

.pages-links {
  justify-content: space-between;
  flex-wrap: wrap;
  li {
    flex-basis: 22%;
  }
  a {
    display: inline-block;
    position: relative;
    padding: 12px 0 8px;
    letter-spacing: 0.7px;

    &:after {
      content: "";
      display: inline-block;
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      height: 1px;
      width: 0%;
      background: $xlight;
      transition: all .4s;
    }
    &:hover,
    &:active {
      &:after {
        width: 100%;
      }
    }
  }
}

.social-links {
  padding-top: 13px;
  li {
    display: inline-block;
    margin-right: 16px;
    &:last-child {
      margin-right: 0;
    }
  }
  a {
    display: inline-block;
  }
}

.social-fb {
  @include sprite($s-fb);
}

.social-tw {
  @include sprite($s-tw);
}

.social-dr {
  @include sprite($s-dr);
}

.social-ig {
  @include sprite($s-ig);
}

.social-pi {
  @include sprite($s-pi);
}

.footer-bottom {
  padding: 5.2% 0 3.2%;
  justify-content: space-between;
  .logo {
    font-size: 37px;
    letter-spacing: -1px;
    flex-basis: 170px;
    margin-bottom: 30px;
  }
  .help,
  .adress {
    flex-shrink: 0;
    padding: 0 10px;
    margin-bottom: 30px;
    h5 {
      margin-bottom: 30px;
    }
    p {
      line-height: 1.5;
      text-transform: none;
      font-size: 16px;
      max-width: 285px;
    }
  }
  .help {
    flex-basis: 280px;
  }
  .adress {
    flex-basis: 370px;
  }
  .button {
    font-size: 14px;
  }
}